import riverMapBg from '@/assets/images/river/river-white-map.png'
import riverActualBg from '@/assets/images/river/river-actual-bg.png'
import riverOpacityBg from '@/assets/images/river/river-opacity-bg.png'
export default {
  name: 'RiverHouse',
  components: {

  },
  data () {
    return {
      step: 3,
      // step: 7,
      riverActualBg: riverActualBg,
      riverOpacityBg: riverOpacityBg,
      riverMapBg: riverMapBg,
      isShowQ315: true,
      riverName: '',
      isShowQ61: true,
      isShowQ63: true
    }
  },
  mounted () {
  },
  methods: {
    goToStepQ31 () {
      this.step = 3.1
    },
    goToStepQ315 () {
      this.step = 3.15
    },
    goToStepQ32 () {
      this.step = 3.2
    },
    goToStepQ3 () {
      if (this.power < 3) {
        this.$store.commit('changePower', this.power)
        // 塞入 local storage
        let vuesList = JSON.parse(localStorage.getItem('vuexList'))
        vuesList.power = this.power
        localStorage.setItem('vuexList', JSON.stringify(vuesList))
        //
      } else {
        this.$store.commit('changePower', 3)
        // 塞入 local storage
        let vuesList = JSON.parse(localStorage.getItem('vuexList'))
        vuesList.power = this.power
        localStorage.setItem('vuexList', JSON.stringify(vuesList))
        //
      }
      this.isShowQ315 = false
      this.step = 3
    },
    goToStepQ4 () {
      this.step = 4
    },
    goToStepQ45 () {
      this.step = 4.5
    },
    goToStepQ99 () {
      // EQ1 結局 1
      this.step = 99
    },
    goToStepQ5 () {
      this.step = 5
    },
    goToStepQ55 () {
      this.step = 5.5
    },
    goToStepQ6 () {
      this.step = 6
    },
    goToStepQ991 () {
      this.step = 99.1
    },
    answerRiverName () {
      if (this.riverName === '旭川河') {
        alert('答對了')
        this.step = 99.2
      } else {
        alert('名稱錯誤，無法收服怪奇。')
      }
    },
    goToStepQ993 () {
      this.step = 99.3
    },
    goToStepQ994 () {
      this.step = 99.4
      this.$store.commit('changeHeader', true)
      this.$store.commit('changeNoteDot', true)
      this.$store.commit('changeNotDotHead', true)
      this.$store.commit('changeFrameDot', true)
      this.$store.commit('riverWin', true)
      this.$store.commit('getRiverCard', 'grey')
      // 塞入 local storage
      let vuesList = JSON.parse(localStorage.getItem('vuexList'))
      vuesList.isRiverWin = true
      vuesList.riverCardType = 'grey'
      localStorage.setItem('vuexList', JSON.stringify(vuesList))
      //
    },
    goToStepQ61 () {
      this.step = 6.1
      this.isShowQ61 = false
    },
    goToStepQ62 () {
      this.step = 6.2
    },
    goToStepQ63 () {
      this.step = 6.3
      this.isShowQ63 = false
    },
    goToStepQ7 () {
      this.step = 7
    },
    goToStepQ1001 () {
      // NE1 結局2
      this.step = 100.1
    },
    goToStepQ8 () {
      this.step = 8
    },
    answerRiverName2 () {
      this.step = 100.2
    },
    goToStepQ1003 () {
      this.step = 100.3
    },
    goToStepQ1004 () {
      this.step = 100.4
      this.$store.commit('changeHeader', true)
      this.$store.commit('changeNoteDot', true)
      this.$store.commit('changeNotDotHead', true)
      this.$store.commit('changeFrameDot', true)
      this.$store.commit('riverWin', true)
      this.$store.commit('getRiverCard', 'grey')
      // 塞入 local storage
