      }

      &-head {
        padding: 4px 16px;
        margin: 220px auto 0px;
        text-align: center;
        background: #95ff00;
        color: black;
        border-radius: 30px;
        z-index: 1;
      }

      &-frame {
        margin-top: 10px;
        max-height: 160px;
        padding: 20px;
        color: white;
        overflow: scroll;
        color: black;
        background: #95ff00;
        z-index: 1;
      }

      &-triangle-box {
        margin-top: 16px;
      }

      &-triangle {
        margin: auto;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 15px 15px 0 15px;
        border-color: black transparent transparent transparent;
      }

      // black btn

      &-all-box {
        position: absolute;
        width: 100vw;
        height: 100vh;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        background-color: rgba(0, 0, 0, 0.8);
        padding-top: 25vh;
        z-index: 1;
      }

      &-all-btn {
        width: 100%;
        color: white;
        text-align: center;
        background: gray;
        margin-top: 30px;
        padding: 20px 10px;
      }

      // Q8

      &-big-fishbird {
        width: 100%;
        height: auto;
        position: absolute;
        top: 4vh;
      }

      &-text {
        color: #95ff00;
        text-align: center;
      }

      &-all-box-quick {
        position: absolute;
        width: 100vw;
        height: 100vh;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        background-color: rgba(0, 0, 0, 0.8);
        padding-top: 15vh;
        z-index: 1;
      }

      // Q245

      &-all-245-box {
        width: 100vw;
        height: 100vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 5vh;
        background-color: rgba(0, 0, 0, 0.8);
        color: white;
        z-index: 1;
      }

      &-245-fishbird {
        width: 30%;
        height: auto;
      }

      &-245-text {
      margin-top: 18vh;
      padding: 16px;
      }

      &-245-input {
        width: 150px;
        padding: 4px;
        color: black;
        margin-top: 10px;
      }

      &-245-go {
        background-color: #95ff00;
        color: black;
        padding: 6px;
        border-radius: 50%;
        text-align: center;
        margin-top: 10px;
      }

      &-last-frame {
        margin-top: 30vh;
        max-height: 200px;
        overflow: scroll;
        color: white;
        z-index: 1;
        padding: 20px;
      }

    }

  }

</style>



